.digg_pagination {
    cursor: default;
    /* self-clearing method: */
}

.digg_pagination a, .digg_pagination span, .digg_pagination em {
    padding: 0.2em 0.5em;
    display: block;
    float: left;
    margin-right: 1px;
}

.digg_pagination .disabled {
    color: #999999;
    border: 1px solid #dddddd;
}

.digg_pagination .current {
    font-style: normal;
    font-weight: bold;
    background: #ef4036;
    color: white;
    border: 1px solid #ef4036;
}

.digg_pagination a {
    text-decoration: none;
    color: #ff3333;
    border: 1px solid #fca420;
}

.digg_pagination a:hover, .digg_pagination a:focus {
    color: white;
    background: #fca420;
    border-color: #ff3333;
}

.digg_pagination .page_info {
    background: #ef4036;
    color: white;
    padding: 0.4em 0.6em;
    width: 22em;
    margin-bottom: 0.3em;
    text-align: center;
}

.digg_pagination .page_info b {
    color: #000033;
    background: #6aa6ed;
    padding: 0.1em 0.25em;
}

.digg_pagination strong {
    color: white;
}

.digg_pagination:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

* html .digg_pagination {
    height: 1%;
}

*:first-child + html .digg_pagination {
    overflow: hidden;
}



